@import "../utils/variables.scss";

.menu {
  color: $colorblue;
  padding: $headerTopPaddind;
  background-color: $colorblue;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPaddind 0;
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding: 8px;
    border-bottom: 1px solid transparent;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    color: #fff;
    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }

  }

  &__call {
    display: block;
    width: max-content;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: solid #fff;
    }
  }
}
